import { NVector3, CPoint } from './lib';

import { Renderer } from './Core/Renderer';
import { GraphClustering } from './Core/GraphClustering';

export class ClusteringCollisionBasedGraph {

    graphClustering: GraphClustering;
    renderer: Renderer;

    mv: NVector3 = new NVector3(0, 0);

    inspection: boolean = true;

    points: CPoint[] = [];

    constructor() {
        this.graphClustering = new GraphClustering(this);
        this.renderer = new Renderer(this);
    }
    public push(x: number, y: number, lon: number, lat: number) {
        const p = new CPoint(x, y);
        p.vec.lon = lon;
        p.vec.lat = lat;
        this.points.push(p);
        this.update();
    }
    public mouseInteraction(x: number, y: number){
        // this.mv = this.optimizer.getCellAtPoint(new NVector3(x, y)).vec;
    }
    public extentChanging(e, toScreen: (x: number, y: number) => number[]) {
        // console.log(e)

        this.points.forEach((p) => {
            const sp = toScreen(p.vec.lon, p.vec.lat)
            p.vec.x = sp[0]
            p.vec.y = sp[1]
            p.updateBounding();
        })

        this.update();
    }
    public update(): void {
        this.graphClustering.update();
        // console.log(this.optimizer.optimizer.optimizer.renderer.annotationInstance.activeCommandController.acProperties.isZooming);
        // if (this.optimizer.optimizer.optimizer.renderer.annotationInstance.activeCommandController.acProperties.isZooming) {
        // this.initGrid(width, height);
        // }
        // console.log(this.points);
    }
    public getCellAtPoint(dp: NVector3) {

    }
    public render(ctx: CanvasRenderingContext2D, isExtentChanging: boolean, toScreen: (x: number, y: number) => number[], toMap: (x: number, y: number) => number[] ) {
        // console.log(isExtentChanging)
        this.points.forEach((p) => {

            if (isExtentChanging) {
                const sp = toScreen(p.vec.lon, p.vec.lat);
                p.vec.x = sp[0];
                p.vec.y = sp[1];
                p.updateBounding();
            } 
            p.render(ctx);

        })

        if (isExtentChanging) {
            this.update();
        }


        this.renderer.Render(ctx, isExtentChanging, toScreen)
    }
}