import { PointCluster } from './PointCluster';
import { CPoint } from '../lib';
import { ClusteringCollisionBasedGraph } from '../';

export class Renderer {

    clustersRPoint: PointCluster[] = [];
    optimizer: ClusteringCollisionBasedGraph;
    constructor(optimizer: ClusteringCollisionBasedGraph) {
        this.optimizer = optimizer;
    }
    public InitGeometry(){
        this.clustersRPoint = [];
    }
    public addClusterByGeometries(geo: CPoint[][] ) {
        for(let i = 0; i < geo.length; ++i) {
            this.clustersRPoint.push(new PointCluster(this, geo[i]));
        }
    }
    public RemoveCluster(cluster: PointCluster ){
        this.clustersRPoint.splice(this.clustersRPoint.indexOf(cluster), 1);

    }
    public Render(ctx: CanvasRenderingContext2D, isStationary: boolean, toScreen: (x: number, y: number) => number[]): void {
        // console.log(this.clustersRPoint.length);
        if (this.clustersRPoint.length > 0) {
            for(let i = 0 ; i < this.clustersRPoint.length; ++i) {
                this.clustersRPoint[i].Draw(ctx, this, isStationary, toScreen);
            }
        }

    }
}