
// import { GeometryBase } from '../../../DrawingEngine/Geometry/Base/GeometryBase';
import { PointCluster } from './PointCluster';
import { ClusteringCollisionBasedGraph } from '..';

import { distance, AABBintersect2D, CPoint } from '../lib';

export class GraphClustering {

    optimizer: ClusteringCollisionBasedGraph;
    pointList: CPoint[] = []
    // listNPoint: CPoint[] = []

    constructor(optimizer: ClusteringCollisionBasedGraph) {
        this.optimizer = optimizer;
    }

    public update(): void {
        this.updatePoints();

        // 1: build connectivity based on collision
        // this.listNPoint = this.BuildConnectivity(this.listNPoint);
        this.pointList = this.BuildConnectivity(this.pointList);
        this.initGeometry();
        // 2: construct clusters graph based on the connectivity
        const geoList = this.buildGraph(this.pointList);

        if (geoList !== undefined){
            this.addClusterByGeometries(geoList);
            // // 3: recursive checking for the collision while repositioning clusters
            this.PostUpdateCollisionBasedonGraph();

            const graphs = this.NeedDecompose();
            if (graphs.length > 0) {
                // 4: tessellate big clusters based on a resolution
                this.SplitClusters(graphs);
                // 5: post-process for merging based on a tolerance
                this.PostMergeCluster();
            }
        }
        // console.log('update done', this.pointList)
    }
    public initGeometry(){
        this.optimizer.renderer.InitGeometry();
    }
    public addClusterByGeometries(outGraphList: CPoint[][] ){
        this.optimizer.renderer.addClusterByGeometries(outGraphList);
    }
    private updatePoints() {
        this.pointList = [];

        for(let i = 0; i < this.optimizer.points.length; ++i) {
            if(this.optimizer.points[i].isHidden ||
                !this.optimizer.points[i].isRenderable ){
                continue
            } else {
                const pt = this.optimizer.points[i];
                pt.collision.Reset();
                this.pointList.push(pt)
            }
        }

    }
    private BuildConnectivity(listPoints: CPoint[]) {
        for(let i = 0; i < listPoints.length; ++i) {
            const pt = listPoints[i];
            for(let j = i + 1 ; j < listPoints.length; ++j) {
                const other = listPoints[j];
                if (!other.isCollapse && other !== pt && AABBintersect2D([pt.collision.minVec, pt.collision.maxVec], [other.collision.minVec, other.collision.maxVec])) {
                    pt.collision.isCollided = true;
                    pt.isCollapse = true;
                    pt.numVisit = 0;
                    pt.collision.neighborShapes.push(other);
                    other.collision.isCollided = true;
                    other.isCollapse = true;
                    other.numVisit = 0;
                    other.collision.neighborShapes.push(pt);
                    // other.collision.neighborShapes.push(pt);
                    // pt.collision.numOrder = numOrder++;
                    // break;
                }
            }
        }
        // console.log(listPoints)
        return listPoints;
    }
    private buildGraph(listPoints: CPoint[]) {
        let outList: CPoint[][] = [];
        for(let i = 0; i < listPoints.length; ++i) {
            let ptList: CPoint[] = [];
            let queue = [listPoints[i]];
            queue[0].collision.isVisited = true;
            ptList.push(queue[0]);
            let safeguard = 0;
            let current = undefined;
            while( (current = queue.shift()) !== undefined ) {
                for(let i = 0; i < current.collision.neighborShapes.length; ++i) {
                    if(!current.collision.neighborShapes[i].collision.isVisited) {
                        current.collision.neighborShapes[i].collision.isVisited = true;
                        queue.push(current.collision.neighborShapes[i] as CPoint);
                        ptList.push(current.collision.neighborShapes[i] as CPoint);
                    }
                }
                if(safeguard++ > 1000) { console.debug('fail to optimize'); return; }
            }
            if (ptList.length > 1) {
                outList.push(ptList);
            }
        }
        return outList;
    }
    public PostUpdateCollisionBasedonGraph() {
        // for(let i = 0; i < this.optimizer.renderer.clustersNPoint.length; ++i) {
        //     this.UpdateByQueue(this.optimizer.renderer.clustersNPoint[i], this.listNPoint);
        // }
        for(let i = 0; i < this.optimizer.renderer.clustersRPoint.length; ++i) {
            this.UpdateByQueue(this.optimizer.renderer.clustersRPoint[i], this.pointList);
        }
    }
    private UpdateByQueue(cluster: PointCluster, geometries: CPoint[]) {
        let remain = 0;
        const queue = [...geometries]
        while(queue.length) {
            const geo = queue.shift();

            if (geo) {
                // console.log(queue.length);
                // safeguard and performance optimization
                if (geo.numVisit > 100) { geo.numVisit = 0; break; }
                if (queue.length === 0) {
                    if(geometries.length < remain++ ) { break; }
                }
                // end safeguard and performance optimization
                if (!geo.collision.isCollided) {
                    if(AABBintersect2D([geo.collision.minVec, geo.collision.maxVec], [cluster.minVec, cluster.maxVec]) ) {
                        geo.collision.isCollided = true;
                        geo.isCollapse = true;
                        geo.numVisit = 0;
                        cluster.AddPost(geo);
                    } else {
                        geo.numVisit++;
                        queue.push(geo);
                    }
                }
            }
        }
    }
    private NeedDecompose(){
        const clusterGraph = [];

        for(let i = 0; i < this.optimizer.renderer.clustersRPoint.length; ++i) {
            if(this.optimizer.renderer.clustersRPoint[i].NeedDecompose()) {
                clusterGraph.push(this.optimizer.renderer.clustersRPoint[i]);
            }
        }
        return clusterGraph;
    }
    private SplitClusters(clusters: PointCluster[]) {
        for(let i = 0; i < clusters.length; ++i) {
            clusters[i].Split();
        }
    }
    private PostMergeCluster(){
        this.PostMergeClusterCompute(this.optimizer.renderer.clustersRPoint);
    }
    private PostMergeClusterCompute(clusters: PointCluster[]){
        for(let i = 0; i < clusters.length - 1; ++i) {
            const cluster = clusters[i];
            for(let j = i + 1; j < clusters.length; ++j) {
                const other = clusters[j]
                if(!other.isVisited) {
                    if(distance(cluster.vec, other.vec ) < cluster.r1 + 20) {
                        other.isVisited = true;
                        cluster.Merge(other);
                    }
                }
            }
        }
    }
}