
// import { GeometryBase } from '../../../DrawingEngine/Geometry/Base';
// import { GTYPE } from '../../../../../../../lib/Enumeration';
import { distance, NVector3, CPoint} from '../lib';
// import { NVector3 } from '../../../../../../../lib/NVector3';
import { Renderer } from './Renderer';

export class PointCluster {
    renderer: Renderer;
    points: CPoint[];
    r0: number = 32 * 0.5;
    r1: number = 48 * 0.5;
    isHover: boolean = false;
    isVisited: boolean = false;
    boundingBoxMin: NVector3 = new NVector3(Number.MAX_SAFE_INTEGER, Number.MAX_SAFE_INTEGER, Number.MAX_SAFE_INTEGER);
    boundingBoxMax: NVector3 = new NVector3(Number.MIN_SAFE_INTEGER, Number.MIN_SAFE_INTEGER, Number.MIN_SAFE_INTEGER);
    minVec: NVector3;
    maxVec: NVector3;
    vec = new NVector3(0, 0, 0);
    private t: number = 1.0;
    private GridResolution = 85;
    private tempVecGridList: NVector3[] = [];
    private tempGeoGridList: CPoint[][][] = [];

    constructor(renderer: Renderer, geometries: CPoint[]) {
        this.renderer = renderer;
        this.points = geometries;
        for(let i = 0; i < this.points.length; ++i) {
            this.vec.x += this.points[i].vec.x;
            this.vec.y += this.points[i].vec.y;
            this.UpdateBoundingBox(this.points[i].vec);
        }
        this.vec.x /= this.points.length;
        this.vec.y /= this.points.length;
        this.minVec = new NVector3(this.vec.x - this.r1, this.vec.y - this.r1, 0);
        this.maxVec = new NVector3(this.vec.x + this.r1, this.vec.y + this.r1, 0);
        // for(let i = 0; i < this.geometries.length; ++i) {
        // this.geometries[i].isAniEasing = true;
        // this.geometries[i].tVec = this.vec;
        // }
    }
    public IsInside(m: NVector3) {
        if(distance(this.vec, m ) < this.r1) {
            this.isHover = true;
            // this.points[0].scene.annotationInstance.activeCommandController.activeMouseCursor.SetActiveCursorHovering();
            // this.points[0].scene.annotationInstance.renderer.needFocus = true;
            // this.points[0].scene.annotationInstance.activeCommandController.acProperties.hasHovering = true;
            return true;
        } else {
            this.isHover = false;
            return false;
        }
    }
    public AddPost(geo: CPoint) {
        // @ts-ignore
        if(this.points.includes(geo)) { return; }
        this.points.push(geo);
        this.vec = new NVector3(0, 0, 0);
        for(let i = 0; i < this.points.length; ++i) {
        this.vec.x += this.points[i].vec.x;
        this.vec.y += this.points[i].vec.y;
        this.UpdateBoundingBox(this.points[i].vec);
        }
        this.vec.x /= this.points.length;
        this.vec.y /= this.points.length;
        this.minVec = new NVector3(this.vec.x - this.r1, this.vec.y - this.r1, 0);
        this.maxVec = new NVector3(this.vec.x + this.r1, this.vec.y + this.r1, 0);
    }
    public NeedDecompose() {
        if (this.boundingBoxMax.x - this.boundingBoxMin.x > this.GridResolution) { return true; }
        if (this.boundingBoxMax.y - this.boundingBoxMin.y > this.GridResolution) { return true; }
        return false;
    }
    public Split() {
        this.DeepRemove();
        const xNum = (this.boundingBoxMax.x - this.boundingBoxMin.x) / this.GridResolution < 1 ? 1: (this.boundingBoxMax.x - this.boundingBoxMin.x) / this.GridResolution;
        const yNum = (this.boundingBoxMax.y - this.boundingBoxMin.y) / this.GridResolution < 1 ? 1: (this.boundingBoxMax.y - this.boundingBoxMin.y) / this.GridResolution;
        const grid = this.buildGrid(xNum, yNum);
        this.BuildSubCluster(grid, xNum, yNum);
    }
    public Merge(other: PointCluster){
        this.points.concat(other.points);
        this.vec = new NVector3(0, 0, 0);
        for(let i = 0; i < this.points.length; ++i) {
            this.vec.x += this.points[i].vec.x;
            this.vec.y += this.points[i].vec.y;
            this.UpdateBoundingBox(this.points[i].vec);
        }
        this.vec.x /= this.points.length;
        this.vec.y /= this.points.length;
        this.minVec = new NVector3(this.vec.x - this.r1, this.vec.y - this.r1, 0);
        this.maxVec = new NVector3(this.vec.x + this.r1, this.vec.y + this.r1, 0);
        other.DeepRemove();
    }
    public DeepRemove(){
        this.renderer.RemoveCluster(this);
    }
    public buildGrid(xNum: number, yNum: number) {
        this.tempGeoGridList = []
        let gridList = [];;
        for(let y = 0; y < yNum; ++y) {
            const theY = this.boundingBoxMin.y + y * this.GridResolution;
            const geoMat: CPoint[][] = [];
            for(let x = 0; x < xNum; ++x) {
                const theX = this.boundingBoxMin.x + x * this.GridResolution;
                gridList.push(new NVector3(theX, theY));
                const geoList: CPoint[]= [];
                geoMat.push(geoList);
            }
            this.tempGeoGridList.push(geoMat);
        }
        this.tempVecGridList = gridList;
        return gridList;
    }
    public BuildSubCluster(vs: NVector3[], xNum: number, yNum: number){
        for(let i = 0 ; i < this.points.length; ++i) {
            this.GetCellAtPoint(this.points[i].vec, xNum, yNum).push(this.points[i]);
        }
        for(let y = 0; y < this.tempGeoGridList.length; ++y) {
            for(let x = 0; x < this.tempGeoGridList[y].length; ++x) {
            if(this.tempGeoGridList[y][x]) {
                if (this.tempGeoGridList[y][x].length === 1) {
                    this.tempGeoGridList[y][x][0].collision.Reset();
                } else if(this.tempGeoGridList[y][x].length > 1) {
                    this.renderer.clustersRPoint.push(new PointCluster(this.renderer, this.tempGeoGridList[y][x]));
                    }
                }
            }
        }
    }
    private GetCellAtPoint(tv: NVector3, xNum: number, yNum: number) {
        let dp: NVector3 = new NVector3(tv.x - this.boundingBoxMin.x, tv.y - this.boundingBoxMin.y);
        // let dp: NVector3 = new NVector3(this.boundingBoxMin.x - tv.x, this.boundingBoxMin.y - tv.y);
        let i: number = Math.round(dp.x / this.GridResolution);
        let j: number = Math.round(dp.y / this.GridResolution);
        if (i >= xNum) { i = xNum - 1; }
        if (j >= yNum) { j = yNum - 1; }
        if (i < 0) { i = 0; }
        if (j < 0) { j = 0; }
        i = Math.round(i);
        j = Math.round(j);
        // console.log(j, i);
        // console.log(this.tempGeoGridList)
        return this.tempGeoGridList[j][i];
    }
    public Draw(ctx: CanvasRenderingContext2D, renderer: Renderer, isPanning: boolean, toScreen: (x: number, y: number) => number[]) {
        this.UpdateTimeer();

        if (isPanning) {
            this.vec = new NVector3(0, 0, 0);
            this.boundingBoxMin = new NVector3(Number.MAX_SAFE_INTEGER, Number.MAX_SAFE_INTEGER, Number.MAX_SAFE_INTEGER);
            this.boundingBoxMax = new NVector3(Number.MIN_SAFE_INTEGER, Number.MIN_SAFE_INTEGER, Number.MIN_SAFE_INTEGER);
            for(let i = 0; i < this.points.length; ++i) {
                this.vec.x += this.points[i].vec.x;
                this.vec.y += this.points[i].vec.y;
                this.UpdateBoundingBox(this.points[i].vec);
            }
            this.vec.x /= this.points.length;
            this.vec.y /= this.points.length;
            this.minVec = new NVector3(this.vec.x - this.r1, this.vec.y - this.r1, 0);
            this.maxVec = new NVector3(this.vec.x + this.r1, this.vec.y + this.r1, 0);
            // this.vec.y -= this.r * 0.5 // offset the ghost center along y
        }

        // const sp = toScreen(this.vec.x, this.vec.y)
        // this.vec.x = sp[0];
        // this.vec.y = sp[1];

        drawingPresetClustering(ctx, this.points, this.vec, this.r0, this.r1, this.isHover, this.t);
        
        if(renderer.optimizer.inspection) {
            ctx.beginPath();
            ctx.rect(this.minVec.x, this.minVec.y, this.maxVec.x - this.minVec.x, this.maxVec.y - this.minVec.y)
            ctx.closePath();
            ctx.fillStyle = 'rgba(0, 0, 0, 0.3)';
            ctx.fill();
            ctx.lineWidth = 0.5;
            ctx.beginPath();
            ctx.rect(this.boundingBoxMin.x, this.boundingBoxMin.y, this.boundingBoxMax.x - this.boundingBoxMin.x, this.boundingBoxMax.y - this.boundingBoxMin.y)
            ctx.closePath();
            ctx.stroke();
            if (this.tempVecGridList.length > 0) {
                for(let i = 0 ; i < this.tempVecGridList.length; ++i) {
                    ctx.beginPath();
                    ctx.rect(this.tempVecGridList[i].x, this.tempVecGridList[i].y, this.GridResolution, this.GridResolution);
                    ctx.closePath();
                    ctx.stroke();
                }
            }

        }
    }
    private UpdateTimeer(){
        if (true ) {
            this.t -= 0.1; // this.points[0].scene.annotationInstance.customization.themeCurrent.properties.timeTransitionA;
            this.t = this.t < 0.4 ? 0.4 : this.t;
        } else {
            this.t += 0.1; // this.points[0].scene.annotationInstance.customization.themeCurrent.properties.timeTransitionB;
            this.t = this.t > 1 ? 1 : this.t;
        }
    }
    private UpdateBoundingBox(vec: NVector3){
        this.boundingBoxMin.x = vec.x < this.boundingBoxMin.x ? vec.x : this.boundingBoxMin.x;
        this.boundingBoxMin.y = vec.y < this.boundingBoxMin.y ? vec.y : this.boundingBoxMin.y;
        this.boundingBoxMax.x = vec.x > this.boundingBoxMax.x ? vec.x : this.boundingBoxMax.x;
        this.boundingBoxMax.y = vec.y > this.boundingBoxMax.y ? vec.y : this.boundingBoxMax.y;
    }
}


const drawingPresetClustering = (
    ctx: CanvasRenderingContext2D,
    geometries: CPoint[],
    vec: NVector3,
    r0: number,
    r1: number,
    isHover: boolean,
    t = 1
  ) => {

    const colorFillHover = '#f00'; 
    const fillColor = '#0f0'; //`rgba(${geometries[0].styleComponent.renderStyle.colorInterpolatedString})`

    // if (renderer.optimizer.optimizer.optimizer.renderer.annotationInstance.implementations.app.INSPECTION) {
    if (isHover) {
      for (let i = 0; i < geometries.length; ++i) {
        ctx.lineWidth = 0.6;
        ctx.strokeStyle = '#f00' // geometries[0].styleComponent.renderStyle.colorFill;
        ctx.beginPath();
        ctx.moveTo(vec.x, vec.y);
        ctx.lineTo(geometries[i].vec.x, geometries[i].vec.y);
        ctx.closePath();
        ctx.stroke();

        // sub points
        ctx.lineWidth = 1;
        ctx.strokeStyle = 'rgba(255, 255, 255, 1)';
        ctx.fillStyle = '#f00' //geometries[0].styleComponent.renderStyle.colorFill;
        ctx.beginPath();
        ctx.arc(geometries[i].vec.x, geometries[i].vec.y, 4, 0, Math.PI * 2);
        ctx.closePath();
        ctx.fill();
        ctx.stroke();
      }
    }

    // center point
    if (isHover) {
      ctx.fillStyle = '#f00' // geometries[0].styleComponent.renderStyle.colorFill;
      ctx.beginPath();
      ctx.arc(vec.x, vec.y, 1.5, 0, Math.PI * 2);
      ctx.closePath();
      ctx.fill();
    } else {
      // if(this.type === GTYPE.POINT_NUMBERED) {
      //   ctx.fillStyle = 'rgba(0, 255, 0, 0.5)';
      // } else if(this.type === GTYPE.POINT_REGULAR) {
      //   ctx.fillStyle = 'rgba(255, 0, 0, 0.5)';
      // }
      ctx.beginPath();
      ctx.arc(vec.x, vec.y, r1, 0, Math.PI * 2);
      ctx.closePath();
      ctx.fillStyle = colorFillHover;
      ctx.fill();
      ctx.beginPath();
      ctx.arc(vec.x, vec.y, r0, 0, Math.PI * 2);
      ctx.closePath();
      ctx.fillStyle = fillColor;
      ctx.fill();

      ctx.fillStyle = `rgba(255, 255, 255, 1)`;
      ctx.font = 'bold 11px Arial';
      // ctx.font='bold 9px NotoSerif-Bold';
      ctx.textAlign = 'center';
      ctx.fillText(`${geometries.length}`, vec.x, vec.y + 4);
    }
  };